.contact{
    padding: 100px 0;
    text-align: center;
    background: #f8f9fa!important;
    &:after{
        clear: both;
    }
    .contact-title{
        margin-bottom: 70px;
        h4{
            font-weight: 400;
            font-size: 16px;
            color: gray;
            text-transform: uppercase;
        }
        h2{
            font-weight: 900;
            font-size: 40px;
            color: #000;
        }
    }
    .contact-form{
        text-align: left!important;
        background: #ffffff;
        padding: 40px 40px 60px;
        overflow: hidden;
        h2{
            font-size: 24px;
            margin-bottom: 48px;
            color: #000;
        }
        .form-group{
           .form-control{
                border-radius: 0!important;
                outline: none;
            
           }
           
        }
        .btn--black-white{
            text-decoration: none;
            color: #fff;
            font-weight: 900;
            font-size: 13px;
            text-transform: uppercase;
            padding: 16px 45px;
            border: 2px solid #000;
            background-color: #000;
            transition: all 0.3s ease;
           
            &:hover {
                background-color: transparent;
                color: #000;
            }
        }
    }
}
.mb-20{
    margin-bottom: 20px !important;
}
