.top_header{
    &:after{
        clear: both;
    }
    background: #f8f9fa;
    padding-top: 20px;
    padding-bottom: 10px;
    .social-icon{
        a{
            padding: 4px 8px 6px;
            background: #000;
            border-radius: 55%;
            text-align: center;

            img{
                height: 16px;
                width: 16px;
            }
        }
    }
    .contact_shop{
        float: right;
        position: relative;
        li{
            display: inline-block;
            margin-left: 20px;
            font-size: 16px;
            color: #000;
            font-weight: 400;
            font-size: 16px;
            i{
                color:#f16821;
                padding-right: 8px;
                font-size: 14px;
            }
        }
    }
}