.special{
    background-image: url(../img/hero_2.jpg);
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    position: relative;
    padding: 80px 0;
    &:before{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0,0,0,0.5);
    }
    h4{
        color: grey;
        font-weight: 500;
        font-size: 16px;
    }
    h2{
        color:#fff;
        font-size: 40px;
        font-weight: 900;
        margin-bottom: 30px;
    }
    p{
        color: #fff;
        font-weight: 300;
        font-size: 20px;
        line-height: 1.2;
        margin-bottom: 50px;

    }
    .btn--white-black{
        text-decoration: none;
        color: #fff;
        font-weight: 900;
        font-size: 13px;
        text-transform: uppercase;
        padding: 16px 45px;
        border: 2px solid #fff;
        background-color: transparent;
        margin-right: 10px;
        transition: all 0.3s ease;
        &:hover {
            background-color: #fff;
            color: #000;
        }
    }
}


















.services{
    &:after{
        clear: both;
    }
    padding: 100px 0 70px;
    .special-title{
        text-align: center;
        margin-bottom: 40px;
        h4{
            color: gray;
            font-size: 16px;
            text-transform: uppercase;
            font-weight: 400;
        }
        h2{
            font-size: 40px;
            color: #000;
            font-weight: 900;
        }
    }
    .special-item{
        display: flex;
        margin-bottom: 40px;
        overflow: hidden;
        .special-icon{
            span{
                font-size: 40px;
                color: #f16821;
            }
            
        }
        .special-content{
            padding-top: 10px;
            margin-left: 15px;
            h3{
                font-size: 20px;
                color: gray;
                font-weight: 500;
            }
            p{
                color: gray;
                font-size: 16px;
                font-weight: 400;
            }
            a{
                text-decoration: none;
                color: #f16821;

            }
        }
       
    }
}