.footer{
    padding: 100px 0;
    &:after{
        clear: both;
    }
    .footer-heading{
        font-size: 16px;
        color: #000;
        text-transform: uppercase;
        font-weight: 900;
        margin-bottom: 20px;
    }
    p{
        color: gray;
        font-size: 16px;
        font-weight: 400;
    }
    ul{
        list-style: none;
        padding-left: 0;
        li{
            margin-bottom: 10px;
        }
    }
    .social-footer{
        li{
            display: inline-block;
            
            margin-right: 20px;
            a{
                color: gray;
                text-decoration: none;
            }
        }
    }
    img{
        display: block;
        width: 100%;
    }
    h3{
        margin-top: 16px;
        color: #000;
        font-size: 20px;
        margin-bottom: 16px;

    }
    .price{
        color: #000;
        margin-bottom: 16px;
        font-weight: 900;
    }
    .btn--black-white{
        text-decoration: none;
        color: #fff;
        font-weight: 900;
        font-size: 12px;
        text-transform: uppercase;
        padding: 6px 20px;
        border: 2px solid #000;
        background-color: #000;
        transition: all 0.3s ease;
        &:hover {
            background-color: transparent;
            color: #000;
        }
    }
}
.ml-auto{
    margin-left: auto;
}
.footer-bt{
    position: relative;
    padding: 20px 0;
    background: #000;
    text-align: center;
    .text{
        color: #fff;
        font-weight: 600;
        margin-bottom: 0;
        .red{
            color: red;
        }
        .black{
            color: #045;
            font-weight: 900;
        }
    }
}

.scroll-top{
    position: fixed;
    right: 20px;
    bottom: 50px;
    background: #691cff;
    color: #fff;
    height: 42px;
    width: 42px;
    line-height: 42px;
    border-radius: 3px;
    text-align: center;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0px 20px 40px -13px #fff;
    
}
