.banner {
    background-image: url("../img/hero_2.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 700px;
    &:after{
        clear: both;
    }
    .banner-content {
        padding-top: 200px;

        h1 {
            font-size: 5rem;
            color: #fff;
            font-weight: 900;
        }

        p {
            font-size: 20px;
            font-weight: 300;
            line-height: 1.5;
            color: #fff;
            margin-bottom: 3rem !important;
            width: 600px;
        }

        .btn-fir {
            text-decoration: none;
            color: #fff;
            font-weight: 900;
            font-size: 13px;
            text-transform: uppercase;
            padding: 16px 45px;
            border: 2px solid #fff;
            background-color: transparent;
            margin-right: 10px;
            transition: all 0.3s ease;
            &:hover {
                background-color: #fff;
                color: #000;
            }
        }

        .btn-sec {
            text-decoration: none;
            padding: 16px 45px;
            border: 2px solid #fff;
            font-weight: 900;
            font-size: 13px;
            text-transform: uppercase;
            color: #000;
            background-color: #fff;
            transition: all 0.3s ease;
            &:hover {
                background-color: transparent;
                color: #fff;
            }
        }
    }
}

