.products {
    text-align: center;
    padding-top: 100px;
    &:after{
        clear: both;
    }
    .products-title {
        margin: auto;

        .pro-title-sm {
            font-size: 13px;
            color: #adb5bd;
            letter-spacing: .2em;
            text-transform: uppercase;
            font-weight: 700;
        }

        .pro-title-lg {
            color: #000;
            font-size: 40px;
            font-weight: 900;
        }

        .lead {
            line-height: 1.7;
            color: gray;
            font-weight: 400;
            font-size: 1rem;
            width: 500px;
        }
    }

    .products-content {
        margin-top: 50px;

        .products-item {
            position: relative;
            overflow: hidden;
            transition: .4s all ease;
            padding-bottom: 40px;
            &:hover {
                .images-box {
                    img {
                        transform: scale(1.1);
                        transition: .4s all ease;
                    }

                    &:before {
                        opacity: 1;
                        top: 20px;
                        left: 20px;
                        right: 20px;
                        bottom: 20px;
                    }
                }
            }

            .images-box {
                position: relative;

                &:before {
                    position: absolute;
                    content: "";
                    border: 4px solid #f16821;
                    transition: .4s all ease-out;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    opacity: 0;
                    z-index: 2;
                }

                margin-bottom: 30px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: auto;
                    display: block;
                    transform: scale(1);
                }
            }

            .content-box {
                h4 {
                    font-size: 18px;
                    font-weight: 500;
                    line-height: 1.2;
                    color: #000;
                }

                .comment {
                    span {
                        color: gray;
                        &:first-child {
                            margin-right: 20px;
                            i {
                                color: #feb518;
                                padding-right: 5px;
                            }
                        }
                        &:last-child{
                            i{
                                color: firebrick;
                                padding-right: 5px;
                            }
                        }
                    }
                }
                .lead{
                    line-height: 1.4;
                    font-size: 14px;
                    font-weight: 400;
                    color: gray;
                    margin-top: 1rem;
                    margin-bottom: 1.5rem!important;
                }
                .btn-fou{
                    text-decoration: none;
                    color: #fff;
                    background-color: #000;
                    padding: 6px 20px;
                    text-transform: uppercase;
                    font-weight: 900;
                    border: 2px solid #000;
                    font-size: 12px;
                    margin-right: 10px;
                    &:hover{
                        color: #000;
                        background-color: #fff;
                    }
                   
                }
                .btn-fiv{
                    text-decoration: none;
                    color: #000;
                    background-color: #fff;
                    padding: 6px 20px;
                    text-transform: uppercase;
                    font-weight: 900;
                    font-size: 12px;
                    border: 2px solid #000;
                    &:hover{
                        color: #fff;
                        background-color: #000;
                    }
                }
            }
        }
    }
}

.site-block {
    position: relative;
    margin-top: 70px;
    background-image: url(../img/hero_2.jpg);
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
    .overlay{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.4);
    }
    .row {
        min-height: 400px;
        .site-til {
            font-size: 28px;
            color: #fff;
            margin-bottom: 30px;
        }

        .form-group {
            display: flex;
            margin-bottom: 30px;
            .form-control {
                display: block;
                background: none;
                border-width: 2px;
                border: 1px solid #fff;
                color: #fff;
                outline: none;
                border-radius: 0 !important;
                font-size: 1rem;
                line-height: 1.5;
                margin-right: 10px;
            }

            .subscribe {
                border-color: #fff;
                background: none;
                color: #000;
                text-transform: uppercase;
                font-size: 12px;
                font-weight: 900;
                padding: 6px 20px;
                border-radius: 0 !important;
                outline: none;
                &:hover{
                    background-color: #fff;
                }
            }

           
        }
        .lead {
            color: #fff;
            font-size: 1rem;
            line-height: 1.5;
            font-weight: 300;
            margin-bottom: 1rem;
        }
    }
}
