.mr-auto{
    margin-right: auto!important;
}
.ml-auto{
    margin-left: auto!important;
}
@keyframes jump {
    0% {
        transform: translateY(0);
    }

    20% {
        transform: translateY(-5px);
    }

    41% {
        transform: translateY(-12px);
    }

    50% {
        transform: translateY(-5px);
    }

    50% {
        transform: translateY(-5px);
    }

    80% {
        transform: translateY(0px);
    }

    100% {
        transform: translateY(0);
    }
}