.blog{
    padding: 100px 0;
    &:after{
        clear: both;
    }
    .blog-title{
        text-align: center;
        margin-bottom: 50px;
        h4{
            font-weight: 400;
            font-size: 16px;
            color: gray;
            text-transform: uppercase;
        }
        h2{
            font-weight: 900;
            font-size: 40px;
            color: #000;
        }
    }
    .blog-item{
        .images-box{
            img{
                display: block;
                width: 100%;
                height: auto;
            }
        }
        .content-box{
            margin-top: 30px;
            a{
                text-decoration: none;
                color:#000;
                font-size: 20px;
                font-weight: 400;
                margin-bottom: 20px;
            }
            .meta{
                color: #b3b3b3;
                font-size: 14px;
                margin-bottom: 20px;
                span{
                    margin: 0 3px;
                }
                a{
                    color: #f16821;
                    text-decoration: none;
                    font-size: 14px;
                }
            }
            p{
                color: gray;
                font-size: 16px;
                font-weight: 400;
            }
            .continue-reading{
                text-decoration: none;
                color: #f16821;
                font-size: 16px;
            }

        }
    }
}