@import url('https://fonts.googleapis.com/css?family=Poppins:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap');

body{
    margin: 0;
    padding: 0;
    font-family: 'Poppins',sans-serif;
    background: #262626;
}

.time-content{
    position: relative;
    margin-bottom: 50px;
    .time-box{
   
        #clock{
            display: flex;
            width: 750px;
            margin: 0 auto;
            div{
                position: relative;
                width: 100px;
                padding: 20px;
                margin: 0 15px;
                background: #ffffff;
                color: #46ddb0;
                border: 1px solid#ddd;
                box-shadow: 0 0 20px gray;
                
                
                &:before{
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 50%;
                    background: rgba(255,255,255,.2);
                }
                span{
                    display: block;
                    text-align: center;
                    &:nth-child(1)
                    {
                        font-size: 48px;
                        font-weight: 650;
                    }
                }
            }
        }
    }
}













