.about-us {
    position: relative;
    padding-top: 70px;
    &:after{
        clear: both;
    }
    .img-about-box {
        position: relative;

        img {
            display: block;
            width: 100%;
            height: auto;
        }

        .about-note {
            position: absolute;
            bottom: -10px;
            right: -20px;
            background: #46ddb0;
            padding: 20px;

            h2 {
                font-size: 45px;
                font-weight: 900;
                color: #ffffff;
            }

            h4 {
                text-transform: uppercase;
                font-size: 1rem;
                color: #ffffff;

            }
        }
    }

    .content-box {
        h4 {
            font-size: 13px;
            color: #adb5bd;
            letter-spacing: .2em;
            text-transform: uppercase;
            font-weight: 700;
        }

        h2 {
            color: #000;
            font-size: 40px;
            font-weight: 900;
            margin-bottom: 30px;
        }

        p {
            color: grey;
            margin-bottom: 30px;
        }

        .btn-eig {
            text-decoration: none;
            color: #fff;
            background-color: #000;
            padding: 6px 20px;
            text-transform: uppercase;
            font-weight: 900;
            border: 2px solid #000;
            font-size: 12px;
            margin-right: 10px;

            &:hover {
                color: #000;
                background-color: #fff;
            }
        }
    }
    .floating-shapes {
        span {
            display: block;
            position: absolute;
            img {
                display: block;
                max-width: 100%;
                height: auto;
            }
            &:nth-child(1) {
                left: 4.8%;
                top: 4.5%;
            }
            &:nth-child(2) {
                left: 9.6%;
                top: 27.2%;
            }
            &:nth-child(3) {
                left: 8.8%;
                bottom: 16%;
            }
            &:nth-child(4) {
                left: 25%;
                bottom: 21%;
            }
            &:nth-child(5) {
                left: 35%;
                top: 6.6%;
            }
            &:nth-child(6) {
                left: 43%;
                bottom: 30%;
            }
            &:nth-child(7) {
                left: 49%;
                top: 50%;
            }
            &:nth-child(8) {
                right: 11.8%;
                top: 33%;
            }
            &:nth-child(9) {
                right: 6%;
                top: 10%;
            }
            &:nth-child(10) {
                right: 4.5%;
                bottom: 33%;
            }
        }
    }
}


// TEAM
.team{
    padding-top: 120px;
    text-align: center;
    padding-bottom: 100px;
    .team-title{
        margin-bottom: 40px;
        h4{
            color: gray;
            font-size: 1rem;
            font-weight: 400;
            text-transform: uppercase;
        }
        h2{
            font-size: 40px;
            font-weight: 900;
            color: #000;
            text-transform: capitalize ;
        }
    }
    .team-item{
        .images-box{
            img{
                display: block;
                width: 75%;
                margin: auto;
            }
        }
        .content-box{
            margin-top: 20px;
            h3{
                font-size: 20px;
                font-weight: 400;
                color: gray;
                margin-bottom: 10px;
            }
            h5{
                font-size: 12px;
                color: gray;
                margin-bottom: 20px;
            }
            p{
                font-size: 16px;
                color: gray;
                font-weight: 400;
                margin-bottom: 20px;
            }
            .social-team{
                a{
                    margin-right: 5px;
                    height: 30px;
                    width: 30px;
                    display: inline-block;
                    background: gray;
                    border-radius: 50%;
                    color: #ffffff;
                   padding-top: 4px;
                    text-align: center;
                    &:hover{
                        background: #000;
                    }
                }
            }
        }
    }
}