.testimonials{
    padding: 100px 0;
    .testimonials-title{
        text-align: center;
        margin-bottom: 50px;
        h4{
            color: grey;
            font-weight: 700;
            font-size: 16px;
        }
        h2{
            color: #000;
            font-weight: 900;
            font-size: 40px;
        }
    }
    .testimonials-item{
        text-align: center;        
        .img-box{
            height: 200px;
            width: 200px;
            border-radius: 50%;
            margin: auto;
            overflow: hidden;
            img{
                display: block;
                width: 100%;
            }
        }
        .content-box{
            margin-top: 30px;
            padding: 0 70px;
            p{
                font-size: 24px;
                font-style: italic;
                color: grey;
                
            }
        }
    }
}
.owl-carousel{
    .owl-nav{
        display: none;
    }
    .owl-dots{
        text-align: center;
        margin-top: 20px;
        display: inline-block;
        width: 100%;
        .owl-dot{
            height: 10px;
            width: 10px;
            margin-right: 5px;
            background: #000;
        }
        .active{
            background: #feb518;
        }
    }
}