.main_header{
    .container{
        padding: 0;
    }
    &:after{
        clear: both;
    }
    .navbar{
        padding: 20px;
        .navbar-brand{
            font-size: 2.5rem;
            font-weight: 900;
            line-height: 1.2;
            text-decoration: none;
            text-align: left;
            span{
                color: #f16821!important;
            }

        }
        #my-nav{
            position: relative;
            width: 100%;
            .navbar-nav{
                position: absolute;
                right: 0;
                .nav-item{
                    display: inline-block;
                    margin-top: 5px;
                    .nav-link{
                        font-size: 16px;
                        padding: 5px 20px;
                        color: #000;
                        display: inline-block;
                        text-decoration: none;
                        font-weight: 700;
                        
                    }
                    
                }
            }
        }
    }
    .selling-sticky{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        background: #fff;
        transition: all 0.5s ease;
        box-shadow: 0px 9px 40px -13px rgba(0, 0, 0, 0.75);
    }
}
.pr-0{
    padding-right: 0;
}
